import { Controller, Block } from '@ali/4ever-cangjie';
import { isSubjectHeading } from './utils';
import { isDisableNewLineGuide } from './helpers';

export default function placeholder() {
  return {
    disable: true,
    when: (controller: Controller, node: Block) => {
      const { document } = controller.value;
      return isSubjectHeading(node) || isDisableNewLineGuide(node, document);
    },
  };
}
